import React from 'react';
import '../../App.css';
import logo from '../../assets/logoRound.PNG';
import clsx from 'classnames';
import { useHistory } from 'react-router-dom';

const Header=(props) =>{
    let history = useHistory();
    console.log(props);
    function onSignUp (){
        history.push("/login");
        console.log(props);
    }
  return (
    
    <header className={'header'}>
    <div className={'headerContainer'}>
    <img className={'logo'}  src={logo}/>
      <div className={'headerRightWrapper'}>
        <div className={'headerRightSubDiv'}>
          <div className={'headerLabel'}>Services near me</div>
          <div className={'headerLabel'}>About Us</div>
          <div onClick={onSignUp} className={'headerLabel'}>SignUp/Login</div>
        </div>
        <div className={'headerContact'}>
        <i className={clsx("fa fa-phone","contactImage")} aria-hidden="true"></i>
        <span className={'contactNo'}>7879555359</span>
        </div>
      </div>
      </div>
    </header>
    
  );
}

export default Header;
