import React from 'react';
import './whyus.css';
import { render } from '@testing-library/react';
import tick from '../../assets/tick1.png';

function WhyUs() {
    let data = {
        wheelsToVet:{title:'WheelsToVet',
            desc:[   
                {value : 'Transport facility to the vet.'},
                {value:'Best Vet for job.'},
                {value:'Veterinary doctors available all the time.'},
                {value:'Assured availability of medicines and vaccinations 24/7.'},
                {value:'Minimum cost of services.'}
            ]},
        Veterinary:{title:'Veterinary',
            desc:[
                {value :'Transport facility to the vet.'},
                {value:'Best Vet for job.'},
                {value:'Veterinary doctors available all the time.'},
                {value:'Assured availability of medicines and vaccinations 24/7.'},
                {value:'Minimum cost of services.'}]},
        Accessories:{title:'Accessories',desc:[
            {value : 'Transport facility to the vet.'},
            {value:'Best Vet for job.'},
            {value:'Veterinary doctors available all the time.'},
            {value:'Assured availability of medicines and vaccinations 24/7.'},
            {value:'Minimum cost of services.'}
        ]},
        Adopt:{title:'Adopt/Shop',desc:[
            {value : 'Transport facility to the vet.'},
            {value:'Best Vet for job.'},
            {value:'Veterinary doctors available all the time.'},
            {value:'Assured availability of medicines and vaccinations 24/7.'},
            {value:'Minimum cost of services.'}]}
        
        
        
    }

         
  return (
    <div class={'container'}>
        <h2>Why Us</h2>
        <div className={'wrapper'}>
        {Box(data.wheelsToVet)}
        {Box(data.Veterinary)}
        {Box(data.Accessories)}
        {Box(data.Adopt)}
        </div>
    </div>
  );
}
function Box(item){

    return(
        <div className={'whyUsBox'}>
        <div className={'whyUsContent'}>
            <div className={'titleWhyUs'}>{item.title}</div>
             <div>
                {
                    item.desc.map((content,index)=>{
                        return(
                            <div className={'whyUsDesc'} key={index}>
                     <img className={'tick'} src={tick}/>
                   <span>  {content.value}</span>
                 </div>
                        )
                    })
                }
                 

             </div>
             </div>
        
        </div>
    )}


export default WhyUs;
