import React, { useState } from 'react';
import clsx from 'classnames';
import servicesBox from './servicesBox.js';
import logo from '../../assets/pet-boarding.png';
import selected from '../../assets/vetSelected.svg';
import vet from '../../assets/vetSelectedFinal.svg';
import './service.css';
import '../../global/global.css';
import SignUp from '../Dialog/Dialog';
import axios from 'axios';
function Services() {
    const [showForm, handleForm] = useState(false);
    function handleService (item){
        if(item.name==='Veterinary Services'){
        handleForm(!showForm);
        }
    }
    function handleSubmit (data){
        axios.post('https://react-node-deployment.herokuapp.com:3000/userInfo',data).then((res)=>{
        handleForm(!showForm);
        console.log(res);
    })
    }

    var services = [{
        name:'Veterinary Services',
        image:vet,           
        selectedImage:selected
    },
    {
        name:'Wheels ready',
        image:vet
    },
    {
        name:'Share Responsibility',
        image:logo
    },
    {
        name:'Grooming',
        image:logo
    },
    {
        name:'Pet Food',
        image:logo
    },{
        name:'Enclosure design',
        image:logo
    }]
    var services2 = [{
        name:'Dog Walking',
        image:vet,           
        selectedImage:selected
    },
    {
        name:'Behaviour Counselling',
        image:vet
    },
    {
        name:'Training',
        image:logo
    },
    {
        name:'Accessories',
        image:logo
    },
    {
        name:'Coming soon..',
        image:logo
    },{
        name:'Coming soon..',
        image:logo
    },


]

  return (
    <div className={clsx('container',"serviceBase",'imageGrid')} >
        {showForm && <SignUp open={showForm} handleForm={handleService} handleSubmit={handleSubmit}  />}
            <div className={'servicesWrapper'}>
            <label className='servicesLabel'> I am looking for</label>
            <div className={'serviceContent'}> 

            {services.map((item,index)=>{
                return(
    <div onClick={handleService.bind(this,item)} className={clsx('boxContainer')}><img className={'serviceLogo'} src={item.image}/>
        <span>{item.name}</span>
    </div>
                )
            })}
            </div>
            <div className={'serviceContent'}> 

{services2.map((item)=>{
    return(
<div className={'boxContainer'}><img className={'serviceLogo'} src={logo}/>
<span>{item.name}</span>
</div>
    )
})}
</div>

         </div>
  
  </div>
  );
}

export default Services;
