import React, {useState} from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Paper from '@material-ui/core/Paper';
import Draggable from 'react-draggable';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import { StylesProvider } from "@material-ui/core/styles";
import "./dialogStyle.css";
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import moment from 'moment';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';
const styles = {
  button: {
    background: "#9a89bb",
    color:'white'
  },
};
function PaperComponent(props) {
  
  return (
    <Draggable handle="#draggable-dialog-title" cancel={'[class*="MuiDialogContent-root"]'}>
      <Paper {...props} />
    </Draggable>
  );
}

 function DraggableDialog(props) {
  const [info, setInfo] = useState({name:'',address:'',contact:'',email:'',breed:'',date:'',time:'',comment:''});
  const [selectedDate, setSelectedDate] = React.useState(null);  /* React.useState(new Date()) */;
  const[error,setError] = useState({name:false,address:false,contact:false,email:false,breed:false,date:false,time:false})

  const handleDateChange = (date) => {
    setSelectedDate(date);
  };
    const handleInputChange=(item,event)=>{
      let userInfo = {...info}
      let formError = {...error}
      debugger;
      if(item==='name'){
        if(event.target.value){
        userInfo.name=event.target.value;
        formError.name=false;
        }else{
          formError.name=true;
        }
      }
      else if(item==='address'){
        if(event.target.value){
        userInfo.address=event.target.value;
        formError.address= false;
      }
        else{
          formError.address=true;
        }
      }
      else if(item==='contact'){
        if(event.target.value){
        userInfo.contact=event.target.value;
        formError.contact=false
        }else{
          formError.contact=true;
        }
      }
      else if(item==='email'){
        if(event.target.value){
        userInfo.email=event.target.value;
        formError.email=false}
        else{
          formError.email=true;
        }
      }
      else if(item==='breed'){
        if(event.target.value){
        userInfo.breed=event.target.value;
        formError.breed=false;
        }else{
          formError.breed=true;
        }
      }
      else if(item==='date'){
        if(event){
        userInfo.date=event;
        formError.date = false;
        setSelectedDate(event);}else{
          formError.date = true;
        }
      }
      else if(item==='time'){
        if(event){
        userInfo.time=event;
        formError.time=false;
        setSelectedDate(event);
        }else{
          formError.time=true
        }
      }
      else if(item==='comment'){
        userInfo.comment=event.target.value;
      } 
      setInfo({...userInfo});
      setError({...formError});
  }
   const handleCancel=()=>{
    setInfo({name:'',email:'',address:''});
    props.handleForm({name:'Veterinary Services'})
  }
  const handleSubmit = ()=>{
    const isError = isValidate();
    if(isError){
      return;
    }else{
    console.log(info);
    props.handleSubmit(info);
    }
  }
  const isValidate = () =>{
    let isError = false;
     let error1 = {...error};
    for (const [key, value] of Object.entries(info)) {
      console.log(`${key}: ${value}`);
      if(value==='' && key!=='comment'){
        isError = true;
        error1[key] = true;
      }
    }
    console.log(isError);
    setError({...error1});
    return isError;
  }
   // const classes = useStyles(props);
  return (
    <StylesProvider injectFirst>
      <Dialog
        open={props.open}
        onClose={props.handleForm.bind(this,{name:'Veterinary Services'})}
        PaperComponent={PaperComponent}
        aria-labelledby="draggable-dialog-title"
      >
        <DialogTitle className={props.classes.button} style={{ cursor: 'move' }} id="draggable-dialog-title">
          Call veterinary home        </DialogTitle>
        <DialogContent>
          <DialogContentText className={props.classes.backgroundColor}>
          <Grid item xs={12}>
          <TextField error = {error.name} value={info.name} onChange={handleInputChange.bind(this,'name')} fullWidth required id="standard-basic" label="Name" />
        </Grid>
        <Grid item xs={12}>
          <TextField error={error.address} value={info.address} onChange={handleInputChange.bind(this,'address')}  fullWidth required   label="Address" />   
        </Grid>
      
        <Grid item xs={12}>
          <TextField error = {error.contact} value={info.contact} onChange={handleInputChange.bind(this,'contact')} fullWidth required   label="Contact Number" />
          
        </Grid>
        <Grid item xs={12}>
          <TextField error={error.email} value={info.email} onChange={handleInputChange.bind(this,'email')}   fullWidth required   label="Email" />
          
        </Grid>
        <Grid item xs={12}>
          <TextField error={error.breed} value={info.breed} onChange={handleInputChange.bind(this,'breed')}  fullWidth required  label="Breed" />
          
        </Grid>
        <Grid item xs={12}>
        <MuiPickersUtilsProvider xs={12} utils={DateFnsUtils}>
        <KeyboardDatePicker
          required
          margin="normal"
          id="date-picker-dialog"
          label="Date picker dialog"
          format="MM/dd/yyyy"
          value={selectedDate}
          onChange={handleInputChange.bind(this,'date')}
          KeyboardButtonProps={{
            'aria-label': 'change date',
          }}
          // defaultValue=''
          fullWidth
          disablePast
          error = {error.date}
        />
        </MuiPickersUtilsProvider>

         {/*  <TextField value={info.date} onChange={handleInputChange.bind(this,'date')}  fullWidth required   label="Date" /> */}
        </Grid>
        
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
        <Grid item xs={12} >
        <KeyboardTimePicker
          required
          margin="normal"
          id="time-picker"
          label="Time picker"
          value={selectedDate}
          onChange={handleInputChange.bind(this,'time')}
          KeyboardButtonProps={{
            'aria-label': 'change time',
          }}
          fullWidth
          disablePast
          error={error.time}
        />
        </Grid>

      </MuiPickersUtilsProvider> 
         {/*  <TextField fullWidth value={info.time} onChange={handleInputChange.bind(this,'time')}  required   label="Time" /> */}
        <Grid item xs={12}>
          <TextField value={info.comment} onChange={handleInputChange.bind(this,'comment')}  fullWidth   label="Comments" />
        </Grid>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleCancel} color="primary">
            Cancel
          </Button>
          <Button onClick={handleSubmit}color="primary">
            Submit
          </Button>
        </DialogActions>
      </Dialog>
      </StylesProvider>
  );
}
export default withStyles(styles)(DraggableDialog);