import React from 'react';
import './cateogry.css';
import clsx from 'classnames';
import feed1 from '../../assets/feed1.jpg';
import feed2 from '../../assets/feed2.jpg';
import feed3 from '../../assets/feed3.jpg';
import feed4 from '../../assets/feed4.jpg';
import feed11 from '../../assets/feed11.jpg';
import feed22 from '../../assets/feed22.jpg';
import feed33 from '../../assets/feed33.jpg';
import feed44 from '../../assets/feed44.jpg';
import z1 from '../../assets/z1.jpg';
import z2 from '../../assets/z2.jpg';
import z3 from '../../assets/z3.jpg';
import z4 from '../../assets/z4.jpg';
function Cateogry(props) {
    var products = [{title:'Brand wheelstovet','key1':feed1,'key2':feed2,'key3':feed3,'key4':feed4},
    {title:'Accessories','key1':feed11,'key2':feed22,'key3':feed33,'key4':feed44},
    {title:'Enclosure Designing','key1':z1,'key2':z2,'key3':z3,'key4':z4}]


  return (
    <div className={clsx('container','cateogryWrapper')}>
        {
            products.map((product,index)=>{
                return(
                    <div className={'boxWrapper'}>
                    <div className={'boxTitle'}><h2 className={'h2Title'}>{product.title}</h2></div>
                    <div className={'box'}>
                        <div className={'boxRowWrapper'}>
                            <div className={'boxRowOne'}>
                                <img className={'boxImages'} src={product.key1}/>
                                {/* <label className={'itemName'}>Dog</label> */}
                            </div>
                            <div className={'boxRowOne'}>
                                <img className={'boxImages'} src={product.key2}/>
                                {/* <label className={'itemName'}>Cat</label> */}
                            </div>
                            
                        </div>
                        <div className={'boxRowWrapper'}>
                        <div className={'boxRowTwo'}>
                                <img className={'boxImages'} src={product.key3}/>
                               {/*  <label className={'itemName'}>Dog</label> */}
                            </div>
                            <div className={'boxRowTwo'}>
                                <img className={'boxImages'}  src={product.key4}/>
                               {/*  <label className={'itemName'}>Cat</label> */}
                            </div>
                        </div>
                    </div>
                    <div className={'seeMoreWrapper'}><span className={'seeMore'}>See More</span></div>
                </div>
                )
            })
        }
       
       
    </div>
    
  );
}

export default Cateogry;
