import React from 'react';
import './product.css';
import clsx from 'classnames';
import product1 from '../../assets/product1.jpg';
import product2 from '../../assets/dog_plate.jpg';
import product3 from '../../assets/product3.jpg';
import product4 from '../../assets/product4.jpg';
import product5 from '../../assets/product5.jpg';
import product6 from '../../assets/product6.jpg';
function Product(props) {
    var products = [{
        image:product1,
        name:'Dog Bed',
        price:'500',
        size:'Medium',
        discount:'NA'
    },
    {
        image:product2,
        name:'Dog Bed',
        price:'500',
        size:'Medium',
        discount:'NA'
    },
    {
        image:product3,
        name:'Dog Bed',
        price:'500',
        size:'Medium',
        discount:'NA'
    },
    {
        image:product4,
        name:'Visus Cat Food',
        price:'500',
        size:'Medium',
        discount:'NA'
    },
    {
        image:product5,
        name:'Cat Bed',
        price:'500',
        size:'Medium',
        discount:'NA'
    }]

  return (
    <div className={'container'}>
    <h2>{props.heading}</h2>
    <div className={
        'productItem'}>
    {products.map((product,index)=>{
        return( 
            <div className="productItem1">
            <div className={"productCarouselContentWrapper"}>
                <img className={'productCarouselImage'} src={product.image} alt="Los Angeles" />
                    <div className={"productCarouselLabelWrapper"}>
                   <span className={'itemDesc'}>{product.name} Gnawlers Calcium Milk Bone 30 pieces mini 270 gm</span>
                    <span>Rs. {product.price}</span>
                </div>
            </div>
        </div>
        )
    })}
    </div>
       
    </div>
    
  );
}

export default Product;
