import React from 'react';
import './toggle.css';

function Toggle() {
  return (
      <React.Fragment>
        <div className={'toggleWrapper'}>
        <label className='toggle-label'>
 <input type='checkbox'/>
	 <span className='back'>
		<span className='toggle'></span>
 		<span className='label off'>Pet</span>
		<span className='label on'>Stray</span>  
	</span>
</label>
  </div>

  </React.Fragment>
    
  );
}

export default Toggle;
