import React from 'react';
import './review.css';
import '../../global/global.css';
import  review from '../../assets/review.jpg';
import clsx from 'classnames';
 import ReviewCarousel from './ReviewCarousel.js';
//import Carousel from '../Carousel/Carousel.js';
function Review() {
  return (
    <React.Fragment>
    <div className={clsx('reviewWrapper','container')} >
      <div className={'imageReviewWrapper'}>
        <img className={'reviewBgImage'} src={review}/>
      </div>
        <div className={clsx('reviewContentWrapper')} >
        <div>
        <h2 className={'reviewTitle'}>Here is what  pet owners have to</h2>
        <h2 className={clsx('reviewTitle','titleTwo')}>say about wheelstovet...</h2>
        </div>
         <ReviewCarousel/> 
        </div>
  </div> 
  </React.Fragment>
  );
}

export default Review;
