import React from 'react';
import '../Carousel/Carousel.css';
import clsx from 'classnames';


function Carousel() {
    var data = [
        {value:"Our cat missed us ... but not very much since she was receiving regular visits from her new friend. She didn't lose any weight and looks great! She also stayed on her medication schedule - an important aspect of life for a senior cat.",
        reviewer:'J.P'
        },
        {value:"Our cat missed us ... but not very much since she was receiving regular visits from her new friend. She didn't lose any weight and looks great! She also stayed on her medication schedule - an important aspect of life for a senior cat.",
        reviewer:'M.P'
        },
        {value:"Our cat missed us ... but not very much since she was receiving regular visits from her new friend. She didn't lose any weight and looks great! She also stayed on her medication schedule - an important aspect of life for a senior cat.",
        reviewer:'K.P'
        },
        {value:"Our cat missed us ... but not very much since she was receiving regular visits from her new friend. She didn't lose any weight and looks great! She also stayed on her medication schedule - an important aspect of life for a senior cat.",
        reviewer:'D.P'
        }
    ]
  return (
  <div id="myCarousel1" className={clsx("carousel slide","myCarouselwrap")} data-ride="carousel" data-interval="false">
    <ol className="carousel-indicators">
      <li className={clsx('list','active')} data-target="#myCarousel1" data-slide-to="0"></li>
      <li className={'list'} data-target="#myCarousel1" data-slide-to="1"></li>
      <li className={'list'} data-target="#myCarousel1" data-slide-to="2"></li>
      <li className={'list'} data-target="#myCarousel1" data-slide-to="3"></li>
    </ol>

    <div className="carousel-inner">

    {
        data.map((item,index)=>{
            return(
                <div key={index} className={clsx("item",index===0?'active':'')}>
                <div className={'reviewCarouselWrapper'}>
                    <div className={'reviewCaraouselContentWrapper'}>
                   <p className={'reviewContent'}> {item.value}</p>
                   <div className={'reviewerName'}><strong>- {item.reviewer}. </strong></div>
                  </div>
              </div>
              </div>
            )
        })
    }

     <a className={clsx("arrow","left carousel-control")} href="#myCarousel1" data-slide="prev">
      <span className={clsx("glyphicon glyphicon-chevron-left",'arrowIcon')}></span>
      <span className="sr-only">Previous</span>
    </a>
    <a className={clsx("right carousel-control","arrow")} href="#myCarousel1" data-slide="next">
      <span className={clsx("glyphicon glyphicon-chevron-right",'arrowIcon')}></span>
      <span className="sr-only">Next</span>
    </a> 
  </div>
</div>
  )
}

export default Carousel;
