import React from 'react';
import './login.css';
import { Router, Route, Switch } from "react-router";
import clsx from 'classnames';
function SignUp() {
   
return(
    <div className={'SignUpWrapper'}>
        <div className={'firstDiv'}>
            <div className={'secondDiv'}>
            <h2 className={'signUpTitle'}>Sign Up</h2>
        <div className={clsx('labels')}>
            <label>Username</label>
            <input className={'inputBox'} type='text'/>
        </div>
        <div className={clsx('labels')}>
            <label>Email</label>
            <input className={'inputBox'} type='text'/>
        </div>
        <div className={clsx('labels')}>
            <label>Password</label>
            <input className={'inputBox'} type='text'/>
        </div>
        <div className={clsx('labels')}>
            <label>Confirm Password</label>
            <input className={'inputBox'} type='text'/>
        </div>
        <div className={'btnWrapper'}><button className={'btn'}>Sign Up</button></div>
        </div>
        <span className={clsx('reLogin','reloginPaading')}>Already have an account?<a className={'reLogin'} href='/'> Login In here</a></span>
        </div>
    </div>
)

}


export default SignUp;
