import React from 'react';
import './search.css';
import Toggle from '../../Component/Toggle/Toggle';
import clsx from 'classnames';
import '../../global/global.css';

function Search() {
  return (
    <div className={clsx('container','imageGrid','searchGrid')}>
    <div className='searchWrapper'>
    <select className={"searchSelectBox"}>
        <option  className={'optionLabels'}>Veterinary Services</option>
        <option className={'optionLabels'}>Wheels Ready</option>
        <option  className={'optionLabels'}>Pet Grooming</option>
        <option  className={'optionLabels'}>Training</option>
        <option  className={'optionLabels'}>Pet Food</option>
        <option  className={'optionLabels'}>Pet Hostel</option>
    </select>
    <div>
      <input className={'searchBtn'} type='button' value="Get pet services"/> 
        </div>
        <Toggle/>
    </div>
  
    </div>
    
  );
}

export default Search;
