import React from 'react';
import './Carousel.css';
import clsx from 'classnames';
import petBoarding from '../../assets/v1.jpg';
import petGrooming from '../../assets/v2.jpg';
import v1 from '../../assets/v3.jpg';
import v2 from '../../assets/v4.jpg';
import v3 from '../../assets/v5.jpg';
import v4 from '../../assets/v6.jpg';
import v5 from '../../assets/v7.jpg';
import v6 from '../../assets/v8.jpg';
import v7 from '../../assets/v9.jpg';
import v8 from '../../assets/v1.jpg';
import v9 from '../../assets/v2.jpg';

function Carousel() {
  return (
<div className="container">
  <h2>Services we offer</h2>  
  <div id="myCarousel" className="carousel slide" data-ride="carousel" data-interval="false">
    <ol className="carousel-indicators">
      <li data-target="#myCarousel" data-slide-to="0" className="active"></li>
      <li data-target="#myCarousel" data-slide-to="1"></li>
      <li data-target="#myCarousel" data-slide-to="2"></li>
    </ol>

    <div className="carousel-inner">
      <div className="item active">
      <div className="item1">
        <div  className={"carouselContentWrapper"}>
          <img className={'carouselImage'} src={v1} alt="Los Angeles" />
        <div className={"carouselLabelWrapper"}>
        Veterinary Services
          </div>
        </div>

        <div className={"carouselContentWrapper"}>
          <img  className={'carouselImage'} src={v2} alt="Los Angeles" />
        <div className={"carouselLabelWrapper"}>Wheels Ready
          </div> 
        </div>

        <div className={"carouselContentWrapper"}>
          <img  className={'carouselImage'} src={v3} alt="Los Angeles" />
        <div className={"carouselLabelWrapper"}>Share Responsibility
          </div> 
        </div>
         
          </div>
      </div>

      <div className="item">
      <div className="item1">
        <div className={"carouselContentWrapper"}>
          <img  className={'carouselImage'} src={v4} alt="Los Angeles" />
        <div className={"carouselLabelWrapper"}>
        Grooming
          </div>
        </div>

        <div className={"carouselContentWrapper"}>
          <img   className={'carouselImage'} src={v5} alt="Los Angeles" />
        <div className={"carouselLabelWrapper"}>
        Pet Hostel </div> 
        </div>

        <div className={"carouselContentWrapper"}>
          <img  className={'carouselImage'} src={v6} alt="Los Angeles" />
        <div className={"carouselLabelWrapper"}>Get Pet
          </div> 
        </div>
         
          </div>
      </div>
    
      <div className="item">
      <div className="item1">
        <div className={"carouselContentWrapper"}>
          <img  className={'carouselImage'} src={v7} alt="Los Angeles" />
        <div className={"carouselLabelWrapper"}>
        Pet Food
          </div>
        </div>

        <div className={"carouselContentWrapper"}>
          <img  className={'carouselImage'} src={v8} alt="Los Angeles" />
        <div className={"carouselLabelWrapper"}>Enclosure design
          </div> 
        </div>

        <div className={"carouselContentWrapper"}>
          <img  className={'carouselImage'} src={v9} alt="Los Angeles" />
        <div className={"carouselLabelWrapper"}>Comfort
          </div> 
        </div>
         
          </div>
      </div>
    </div>

    <a className={clsx("arrow","left carousel-control")} href="#myCarousel" data-slide="prev">
      <span className="glyphicon glyphicon-chevron-left"></span>
      <span className="sr-only">Previous</span>
    </a>
    <a className={clsx("right carousel-control","arrow")} href="#myCarousel" data-slide="next">
      <span className="glyphicon glyphicon-chevron-right"></span>
      <span className="sr-only">Next</span>
    </a>
  </div>
</div>
  )
}

export default Carousel;
